body {
  background: #416e8e; }

h1 {
  orientation: center;
  margin: center;
  padding: 20px; }

nav ul {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none; }

nav li {
  margin: 5px;
  background: #000; }

nav a {
  display: block;
  padding: 5px 15px;
  text-decoration: none;
  color: #416e8e; }

.selector-a {
  padding: 5px;
  border: orange 1px solid;
  background: #416e8e;
  box-shadow: 1px 1px 1px #333; }

.selector-a {
  float: left; }

.selector-a, .selector-b {
  background: orange; }

.testing {
  width: 71.42857%; }
